<template>
  <v-card height="100%">
    <v-card-title>
      <!--  or    -->
      {{data.date}}
      {{data.is}}
    </v-card-title>
    <v-card-text > 
      <!--      or   v-for  -->
      <v-layout row>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <v-subheader></v-subheader>
      <!-- <v-rating v-model="rating"></v-rating> -->
      </v-layout>
      <v-container
      id="scroll-target"
      style="height: 300px"
      class="scroll-y"
      >
      <div v-for="account in this.data.accounts">
        <AccountComp v-bind:data="{category:account.category, name:account.name, price:account.price, rate:account.rate}"></AccountComp>
      </div>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-layout row justify-center>
        <v-flex xs10>
          <v-btn slot="activator" color="orange" dark v-on:click="addbtn" id="add_b"></v-btn>
          <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
              <v-card-title>
                <span class="headline"> </span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap>
                    <v-flex xs12 class="addData">
                      : {{data.date}}
                      <!-- <v-text-field label="Legal first name*" required></v-text-field> -->
                    </v-flex>
                    <v-flex xs12 class="addData">
                      <br>
                      : {{data.is}}
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="" required v-model="addPrice"></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field label="" required v-model="addName"></v-text-field>
                    </v-flex> 
                    <v-flex xs12>
                      <span v-if="data.is==''">
                      <v-select
                        :items="['', '', '', '', '', '']"
                        label=""
                        required
                        v-model="addCategory"
                      ></v-select>
                      </span>
                      <span v-else>
                        <v-select
                        :items="['', '/', '', '/', '/', '', '/', '', '', '/', '', '']"
                        label=""
                        required
                        v-model="addCategory"
                        ></v-select>
                      </span>
                    </v-flex>
                    <v-flex xs12>
                      <v-rating v-model="addRate"></v-rating>
                    </v-flex>
                  </v-layout>
                </v-container>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
                <v-btn color="blue darken-1" flat @click="dialog = false" v-on:click="add">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-card-actions>
      
  </v-card>
</template>

<script>
import AccountComp from "../components/AccountComp";


  export default{
    name : 'AccountShow',
    props:["data"],
    methods:{
      add:async function(){
        await this.$http.post('/account/create', {
          id: this.data.id,
          year: parseInt(this.data.date.slice(0,4)),
          month: parseInt(this.data.date.slice(5,7)),
          day: parseInt(this.data.date.slice(8,10)),
          is: this.data.is,
          price: this.addPrice,
          name:this.addName,
          category: this.addCategory,
          rate: this.addRate
          })
          .then((result)=>{
            console.log(result)
          })
          .catch((err)=>{
            console.log(err)
          })
          // this.get_accounts()
      },
      addbtn:function(){
        if(this.data.date==null){
          alert('  ');
          alert(this.data.id)
          this.dialog=false;
        }
        else this.dialog=true;
        console.log(this.data.accounts)
      },
      // get_accounts:function(){
      //   var api = 'http://localhost:3000/account/list/'+this.data.id+'/'+this.data.date+'/'+this.data.is;
      //   console.log(api)
      //   this.$http.get(api)
      //   .then((result)=>{
      //       this.data.accounts = result.data
      //       console.log(this.data.accounts)
      //       alert('hee')
      //   })
      //   .catch((err)=>{
      //       console.log(err)
      //   })
      // }
    },
    data:function(){
      return{
        addRate:null,
        addName:null,
        addCategory:null,
        addPrice:null,
        dialog: false,
        accounts:this.data.accounts
      }
    },
    components:{
      AccountComp
    }
  }
</script>

<style>
.addData{
  font-size: 18px
}
#add_b{
  align-content: center
}
</style>



